export default "import React, {Component} from 'react';\r\nimport '../../map.scss';\r\n\r\nconst google = window.google;\r\n\r\nconst _ = require('lodash');\r\nconst {compose, withProps, lifecycle} = require('recompose');\r\nconst {\r\n  withScriptjs,\r\n  withGoogleMap,\r\n  GoogleMap,\r\n  Marker,\r\n} = require('react-google-maps');\r\nconst {\r\n  SearchBox,\r\n} = require('react-google-maps/lib/components/places/SearchBox');\r\n\r\nconst MapWithASearchBoxs = compose(\r\n  withProps({\r\n    googleMapURL:\r\n      'https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=places,geometry,drawing&key=AIzaSyC9dEjps9u3AP1ce5dxcXCTYDRUege490k',\r\n    loadingElement: <div className='cr-embed-responsive-item' />,\r\n    containerElement: (\r\n      <div className='cr-embed-responsive cr-embed-responsive-21by9' />\r\n    ),\r\n    mapElement: <div className='cr-embed-responsive-item' />,\r\n  }),\r\n  lifecycle({\r\n    componentWillMount() {\r\n      const refs = {};\r\n\r\n      this.setState({\r\n        bounds: null,\r\n        center: {\r\n          lat: 41.9,\r\n          lng: -87.624,\r\n        },\r\n        markers: [],\r\n        onMapMounted: (ref) => {\r\n          refs.map = ref;\r\n        },\r\n        onBoundsChanged: () => {\r\n          this.setState({\r\n            bounds: refs.map.getBounds(),\r\n            center: refs.map.getCenter(),\r\n          });\r\n        },\r\n        onSearchBoxMounted: (ref) => {\r\n          refs.searchBox = ref;\r\n        },\r\n        onPlacesChanged: () => {\r\n          const places = refs.searchBox.getPlaces();\r\n          const bounds = new google.maps.LatLngBounds();\r\n\r\n          places.forEach((place) => {\r\n            if (place.geometry && place.geometry.viewport) {\r\n              bounds.union(place.geometry.viewport);\r\n            } else {\r\n              bounds.extend(place.geometry.location);\r\n            }\r\n          });\r\n          const nextMarkers = places.map((place) => ({\r\n            position: place.geometry.location,\r\n          }));\r\n          const nextCenter = _.get(\r\n            nextMarkers,\r\n            '0.position',\r\n            this.state.center,\r\n          );\r\n\r\n          this.setState({\r\n            center: nextCenter,\r\n            markers: nextMarkers,\r\n          });\r\n          // refs.map.fitBounds(bounds);\r\n        },\r\n      });\r\n    },\r\n  }),\r\n  withScriptjs,\r\n  withGoogleMap,\r\n)((props) => (\r\n  <GoogleMap\r\n    ref={props.onMapMounted}\r\n    defaultZoom={15}\r\n    center={props.center}\r\n    defaultOptions={{mapTypeControl: false}}\r\n    onBoundsChanged={props.onBoundsChanged}>\r\n    <SearchBox\r\n      ref={props.onSearchBoxMounted}\r\n      bounds={props.bounds}\r\n      controlPosition={google.maps.ControlPosition.TOP_LEFT}\r\n      onPlacesChanged={props.onPlacesChanged}>\r\n      <input\r\n        type='text'\r\n        placeholder='Enter search location'\r\n        style={{\r\n          boxSizing: `border-box`,\r\n          border: `1px solid transparent`,\r\n          width: `240px`,\r\n          height: `32px`,\r\n          marginTop: `27px`,\r\n          padding: `0 12px`,\r\n          borderRadius: `3px`,\r\n          boxShadow: `0 2px 6px rgba(0, 0, 0, 0.3)`,\r\n          fontSize: `14px`,\r\n          outline: `none`,\r\n          marginLeft: '20px',\r\n          textOverflow: `ellipses`,\r\n        }}\r\n      />\r\n    </SearchBox>\r\n    {props.markers.map((marker, index) => (\r\n      <Marker key={index} position={marker.position} />\r\n    ))}\r\n  </GoogleMap>\r\n));\r\n\r\nexport default class MapWithASearchBox extends Component {\r\n  render() {\r\n    return (\r\n      <MapWithASearchBoxs\r\n        containerElement={\r\n          <div className='cr-embed-responsive cr-embed-responsive-21by9' />\r\n        }\r\n        mapElement={<div className='cr-embed-responsive-item' />}\r\n      />\r\n    );\r\n  }\r\n}\r\n";